import React, { useMemo, useState, useEffect, useRef } from 'react';
import { TrendingUp, ArrowRight, RefreshCw, Flame } from 'lucide-react';
import { EnhancedProductCard } from '../product/EnhancedProductCard';
import { getStockLevel } from '../../utils/helpers';
import SectionHeader from './SectionHeader';
import { getLikedProducts, getViewedProducts, trackSectionEvent } from '../../services/userPreferencesService';
import ProductCarousel from '../ui/ProductCarousel';

/**
 * Legnpszerbb  vals adat: akcis termkek (nagy kedvezmny) + tbbi r szerint, max 8.
 */
const PAGE_SIZE = 12;
const SECTION_ID = 'most-popular';

const sliceWrap = (items, start, size) => {
  if (items.length <= size) return items;
  const end = start + size;
  if (end <= items.length) return items.slice(start, end);
  const first = items.slice(start);
  const rest = items.slice(0, end - items.length);
  return [...first, ...rest];
};

export default function MostPopularSection({ products = [], onProductClick, onToggleWishlist, wishlist = [], onViewAll, onAddToCart, contextLabel = '', rotationTick = 0 }) {
  const [page, setPage] = useState(0);
  const [period, setPeriod] = useState('weekly');
  const [autoRotate, setAutoRotate] = useState(false);
  const [isInteracting, setIsInteracting] = useState(false);
  const [isInView, setIsInView] = useState(true);
  const sectionRef = useRef(null);
  const mostPopular = useMemo(() => {
    if (!products.length) return [];
    const inStock = products.filter((p) => (p.inStock ?? p.in_stock ?? true));
    const likedIds = getLikedProducts();
    const viewed = getViewedProducts(20);
    const viewedIds = viewed.map((p) => p.id);
    const withDiscount = [...inStock]
      .filter((p) => p.salePrice && p.price > p.salePrice)
      .sort((a, b) => (b.price - (b.salePrice || 0)) - (a.price - (a.salePrice || 0)));
    const ids = new Set(withDiscount.map((p) => p.id));
    const rest = [...inStock]
      .filter((p) => !ids.has(p.id))
      .sort((a, b) => (b.price || 0) - (a.price || 0));
    const combined = [...withDiscount, ...rest];
    return combined.sort((a, b) => {
      const stockA = getStockLevel(a) ?? 0;
      const stockB = getStockLevel(b) ?? 0;
      const scoreA = (likedIds.includes(a.id) ? 4 : 0) + (viewedIds.includes(a.id) ? 2 : 0) + Math.min(stockA, 20) * 0.1;
      const scoreB = (likedIds.includes(b.id) ? 4 : 0) + (viewedIds.includes(b.id) ? 2 : 0) + Math.min(stockB, 20) * 0.1;
      return scoreB - scoreA;
    });
  }, [products]);
  const weeklySet = useMemo(() => mostPopular.slice(0, PAGE_SIZE * 2), [mostPopular]);
  const monthlySet = useMemo(() => {
    const start = PAGE_SIZE * 2;
    const end = PAGE_SIZE * 4;
    return mostPopular.slice(start, end).length > 0 ? mostPopular.slice(start, end) : mostPopular;
  }, [mostPopular]);
  const activeSet = period === 'weekly' ? weeklySet : monthlySet;
  const visibleProducts = useMemo(() => {
    const start = (page * PAGE_SIZE) % Math.max(1, activeSet.length);
    return sliceWrap(activeSet, start, PAGE_SIZE);
  }, [activeSet, page]);
  const saleCount = useMemo(() => mostPopular.filter((p) => p.salePrice && p.price > p.salePrice).length, [mostPopular]);
  const inStockCount = useMemo(() => mostPopular.length, [mostPopular]);

  useEffect(() => {
    if (!autoRotate || isInView || isInteracting) return undefined;
    const t = setInterval(() => {
      setPeriod((prev) => (prev === 'weekly' ? 'monthly' : 'weekly'));
    }, 8000);
    return () => clearInterval(t);
  }, [autoRotate, isInView, isInteracting]);

  useEffect(() => {
    trackSectionEvent(SECTION_ID, 'section_impression');
  }, []);

  useEffect(() => {
    if (!rotationTick || isInView || isInteracting) return;
    setPage((p) => p + 1);
  }, [rotationTick, isInView, isInteracting]);

  useEffect(() => {
    if (typeof window === 'undefined' || !('IntersectionObserver' in window)) return;
    if (!sectionRef.current || !(sectionRef.current instanceof Element)) return;
    const observer = new IntersectionObserver(
      ([entry]) => {
        setIsInView(entry.isIntersecting);
      },
      { threshold: 0.15 }
    );
    try {
      observer.observe(sectionRef.current);
    } catch (err) {
      return;
    }
    return () => observer.disconnect();
  }, []);

  if (visibleProducts.length === 0) return null;

  return (
    <section
      ref={sectionRef}
      className="section-shell section-world section-world--popular py-10 sm:py-12 lg:py-16"
      aria-labelledby="most-popular-heading"
      aria-label="Legnpszerbb termkek"
      role="region"
      data-section="most-popular"
    >
      <div className="w-full max-w-[2000px] mx-auto px-0 lg:px-10 xl:px-16">
        <div className="section-frame">
          <SectionHeader
            id="most-popular-heading"
            title="Legnpszerbb"
            subtitle="A vsrlk kedvencei s a legjobb ajnlatok"
            Icon={TrendingUp}
            accentClass="from-amber-500 to-orange-600"
            eyebrow="Felfedezs"
            badge={saleCount > 0 ? `${saleCount} akcis termk` : 'Prmium vlogats'}
            contextLabel={contextLabel}
            prominent
            className="border border-amber-100 shadow-sm section-header-hero section-header-hero--popular"
            meta={`Raktron: ${inStockCount.toLocaleString('hu-HU')}  sszesen: ${mostPopular.length.toLocaleString('hu-HU')}`}
            helpText="Csak kszleten lv termkek"
            actions={
              <>
                <div className="inline-flex items-center rounded-full bg-white border border-gray-100 shadow-sm p-1">
                  {[
                    { id: 'weekly', label: 'Heti kedvencek' },
                    { id: 'monthly', label: 'Havi toplista' }
                  ].map((item) => (
                    <button
                      key={item.id}
                      type="button"
                      onClick={() => {
                        setAutoRotate(false);
                        setPeriod(item.id);
                      }}
                      className={`px-3 py-1.5 rounded-full text-xs font-semibold transition-colors min-h-[36px] ${
                        period === item.id
                          ? 'bg-amber-500 text-white'
                          : 'text-gray-600 hover:bg-gray-100'
                      }`}
                    >
                      {item.label}
                    </button>
                  ))}
                </div>
                <button
                  type="button"
                  onClick={() => setPage((p) => p + 1)}
                  className="inline-flex items-center gap-2 px-3 py-2 rounded-lg text-amber-700 bg-amber-50 border border-amber-100 hover:bg-amber-100 transition-colors text-sm font-semibold min-h-[44px]"
                  aria-label="j vlogats"
                >
                  <RefreshCw className="w-4 h-4" />
                  j vlogats
                </button>
                {onViewAll && (
                  <button
                    type="button"
                    onClick={onViewAll}
                    className="inline-flex items-center gap-2 text-primary-600 font-semibold hover:text-primary-700 focus:outline-none focus-visible:ring-2 focus-visible:ring-primary-400 rounded-lg px-3 py-2 min-h-[44px]"
                  >
                    sszes megtekintse <ArrowRight className="w-4 h-4" />
                  </button>
                )}
              </>
            }
          />
          <ProductCarousel className="mt-2" onInteractionChange={setIsInteracting}>
            {visibleProducts.map((product, index) => {
              const stockLevel = getStockLevel(product);
              const highlightBadge = stockLevel !== null && stockLevel <= 3 ? `Utols ${stockLevel} db` : '';
              return (
                <EnhancedProductCard
                  key={product.id}
                  product={product}
                  onToggleWishlist={onToggleWishlist}
                  isWishlisted={wishlist.includes(product.id)}
                  onQuickView={onProductClick}
                  onAddToCart={onAddToCart || (() => {})}
                  index={index}
                  highlightBadge={highlightBadge}
                  sectionId="most-popular"
                  showFeedback
                  size="compact"
                  tone="popular"
                />
              );
            })}
          </ProductCarousel>
          <div className="mt-5 flex flex-wrap items-center gap-2 text-xs text-gray-500">
            <span className="inline-flex items-center gap-1.5 px-2.5 py-1 rounded-full bg-white border border-amber-100 text-amber-700 font-semibold">
              <Flame className="w-3.5 h-3.5" />
              Most prg
            </span>
            <span>Dinamikus vlogats a kszleten lv s akcis termkekbl.</span>
          </div>
        </div>
      </div>
    </section>
  );
}
